body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.slider-container {
    width: 3rem;
    height: 10rem;
    border: 1px solid rgb(109, 109, 109);
    border-width: 0 1px;
    background-color: #3B3B3B;
    display: flex;
    flex-direction: column-reverse;
}

.value-display{
    background-color: #0084A1;
    width: 100%;
}
body {
    background-color: #3B3B3B
}

.page {
    padding: 0 10%
}

.page-title {
    color: white;
    text-align: center
}

.subtitle {
    color: #FF6F61
}

.body-text {
    color: white
}

strong {
    color: #FF6F61
}
.value-text {
    color: white;
    font-size: 0.7rem;
    margin-top: 0px;
    margin-left: auto;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    left: 0;
    right: 0;
    pointer-events: none;

}

.knob-container {
    display: inline-block;
    /* position: relative; */
    text-align: center;
    margin: 0.5em;
}

.label {
    color: white;
    font-size: 0.7rem;
    position: relative;
    top: -15px;
    pointer-events: none;
}
